import { HorizontalMatchStrategy } from "../Match/MatchStrategies/HorizontalMatchStrategy";
import { MatchStrategy } from "../Match/MatchStrategies/IMatchStrategy";
import { VerticalMatchStrategy } from "../Match/MatchStrategies/VerticalMatchStrategy";
import { Piece } from "../Piece/Piece";

export class MatchChecker {
    private matchStrategies: MatchStrategy[] = [];
    public abc = 12;

    constructor() {
        this.matchStrategies.push(new HorizontalMatchStrategy());
        this.matchStrategies.push(new VerticalMatchStrategy());
    }

    public async checkForMatches(piece: Piece, grid: Piece[][])  {//Piece[] {
     console.log("gelmemeliydi");
     return;
     
        let matchedPieces: Piece[] = [];

        for (const strategy of this.matchStrategies) {
            const matches = strategy.checkMatch(piece, grid);
            if (matches.length >= 3) {
                matchedPieces = matchedPieces.concat(matches);
            }
        }

        return matchedPieces;
    }
}